/*--------------------------------------------------------------
2.0 General
--------------------------------------------------------------*/
* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

html {
  font-size: 14px;
}

body {
  font-family: 'Roboto', sans-serif;
  font-size: 100%;
  /* -webkit-scroll-snap-type: mandatory;
  -ms-scroll-snap-type: mandatory;
  scroll-snap-type: mandatory;
  -webkit-scroll-snap-points-y: repeat(100vh);
  -ms-scroll-snap-points-y: repeat(100vh);
  scroll-snap-points-y: repeat(100vh);
  -webkit-scroll-snap-type: y mandatory;
  -ms-scroll-snap-type: y mandatory;
  scroll-snap-type: y mandatory; */
  color: #fff;
}

.tablet {
  display: none;
}

.container {
  max-width: 960px;
}

.row {
  margin-right: 0;
  margin-left: 0;
}

/*--------------------------------------------------------------
2.0 Header
--------------------------------------------------------------*/
header {
  height: 100vh;
  background-image: url(img/BMW-S1000R-bg2.jpg);
  background-attachment: fixed;
  background-color: #222;
  background-repeat: no-repeat;
  background-position: 43%;
  background-size: cover;
}

header:before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 70%, rgba(0, 0, 0, 0.6) 100%);
}

header a {
  color: #fff;
}

#header-img {
  width: 50px;
}

/* Menu */
.menu-icon-flex {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
  padding: 16px;
}

.sidenav {
  height: 100%;
  width: 0;
  position: fixed;
  z-index: 1;
  top: 0;
  right: 0;
  background-color: #fff;
  overflow-x: hidden;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  padding-top: 60px;
}

.sidenav a {
  padding: 20px 10px;
  margin: 10px 10px;
  text-decoration: none;
  font-size: 16px;
  color: #222;
  display: block;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  border-bottom: 1px solid #ddd;
  text-transform: uppercase;
}

.sidenav a:last-child {
  border-bottom: 0;
}

.sidenav a:hover {
  color: #222;
  opacity: 0.8;
}

.sidenav .closebtn {
  position: absolute;
  top: 0;
  right: 25px;
  font-size: 36px;
  margin-left: 50px;
}

#mySidenav.is-active {
  width: 50%;
}

/* Menu burger icon */
.menu-toggle {
  position: relative;
  display: block;
  width: 30px;
  height: 16px;
  background: transparent;
  border-top: 2px solid;
  border-bottom: 2px solid;
  color: #fff;
  font-size: 0;
  -webkit-transition: all 0.25s ease-in-out;
  transition: all 0.25s ease-in-out;
}

.menu-toggle:before, .menu-toggle:after {
  content: '';
  display: block;
  width: 100%;
  height: 2px;
  position: absolute;
  top: 50%;
  left: 50%;
  background: currentColor;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  transition: -webkit-transform 0.25s ease-in-out;
  -webkit-transition: -webkit-transform 0.25s ease-in-out;
  transition: transform 0.25s ease-in-out;
  transition: transform 0.25s ease-in-out, -webkit-transform 0.25s ease-in-out;
}

.menu-toggle {
  z-index: 5;
}

.menu-toggle:focus {
  outline: none;
}

.menu-toggle:hover {
  color: #fff;
}

.menu-toggle.is-active {
  border-color: transparent;
  color: #222;
}

.menu-toggle.is-active:before {
  -webkit-transform: translate(-50%, -50%) rotate(45deg);
  -ms-transform: translate(-50%, -50%) rotate(45deg);
  transform: translate(-50%, -50%) rotate(45deg);
}

.menu-toggle.is-active:after {
  -webkit-transform: translate(-50%, -50%) rotate(-45deg);
  -ms-transform: translate(-50%, -50%) rotate(-45deg);
  transform: translate(-50%, -50%) rotate(-45deg);
}

button {
  border: none;
  cursor: pointer;
  outline: none;
}

.arrow-down {
  /* center element at bottom */
  width: 24px;
  position: absolute;
  bottom: 0;
  right: 25%;
  left: 50%;
  margin-left: -12px;
  padding-bottom: 8px;
}

/* end burger icon animation */
.title {
  text-align: center;
  color: #fff;
  text-transform: uppercase;
}

.title h1 {
  font-size: 16px;
  font-size: 1.143em;
  font-weight: 100;
}

.title span {
  display: block;
  font-size: 24px;
  font-size: 1.714em;
  font-weight: 400;
}

.header-bottom {
  display: block;
  position: absolute;
  bottom: 0;
  width: 100%;
  margin-bottom: 16px;
  padding-bottom: 16px;
  text-align: center;
}

.header-bottom .col-4 {
  border-right: 1px solid #fff;
  opacity: 0.9;
}

.header-bottom .col-4:last-child {
  border-right: 0;
}

.header-bottom p:first-child {
  font-size: 22px;
}

.header-bottom p:nth-child(2) {
  font-size: 10.1px;
}

/* end of header */
/*--------------------------------------------------------------
3.0 Main
--------------------------------------------------------------*/
main {
  color: #222;
}

.section {
  padding-top: 50px;
}

.section h2 {
  padding-bottom: 16px;
  font-weight: 400;
}

.section p {
  padding-bottom: 24px;
  font-size: 18px;
  font-weight: 100;
  line-height: 1.68;
}

.img-bg-dynamics {
  height: 320px;
  width: auto;
  background-image: url(img/BMW-S1000R-front-small.jpg);
  background-color: #222;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.img-bg-engine {
  height: 320px;
  width: auto;
  background-image: url(img/BMW-S1000R-willy-small.jpg);
  background-color: #222;
  background-repeat: no-repeat;
  background-position: left;
  background-size: cover;
}

.img-bg-specifications {
  height: 320px;
  width: auto;
  background-image: url(img/BMW-S1000R-bg3-sm.jpg);
  background-color: #222;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.content {
  padding-bottom: 50px;
}

/* source: https://benmarshall.me/responsive-iframes/ */
.iframe-container {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
  padding-top: 56.25%;
}

.iframe-container iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 0;
}

/* 4x3 Aspect Ratio */
/* .iframe-container-4x3 {
  padding-top: 75%;
} */
#specifications h2 {
  text-align: center;
}

#specifications h2 span {
  display: block;
  font-weight: 100;
  font-size: 24px;
}

#specifications h3 {
  font-weight: 100;
  padding-bottom: 16px;
}

#specifications h4:before {
  display: block;
  content: '';
  width: 26px;
  height: 1px;
  margin: 0 0 8px;
  background-color: rgba(0, 0, 0, .5);
}

#specifications p {
  padding-bottom: 16px;
}

#js-expand-specs {
  display: none;
}

/* buttons */
button:focus {
  outline: 0;
}

.cta {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.btn-plus {
  background: transparent;
  border: 2px solid black;
  border-radius: 50%;
  transition: 0.5s;
}

.btn-more {
  padding-left: 8px;
  font-size: 12px;
  text-transform: uppercase;
}

.cta a {
  margin-left: 16px;
  padding: 8px 30px;
  font-size: 12px;
  color: #222;
  border: 2px solid #222;
  border-radius: 100px;
  text-transform: uppercase;
  transition: 0.5s;
}

.cta a:hover,
.btn-plus:hover {
  text-decoration: none;
  background-color: #222;
  color: #fff;
}

/* form */
.form-control {
  background: rgba(220, 220, 220, 0.4) !important;
  border-radius: 20px !important;
  border: 0;
}

.form-group {
  margin-bottom: 32px;
}

input[type="submit"] {
  padding: 8px 30px;
  font-size: 12px;
  background-color: #222;
  color: #fff;
  border: 2px solid #222;
  border-radius: 100px;
  text-transform: uppercase;
  transition: 0.5s;
}

input[type="submit"]:hover {
  text-decoration: none;
  color: #222;
  background: transparent;
}

/*--------------------------------------------------------------
4.0 Footer
--------------------------------------------------------------*/
footer {
  margin-top: 40px;
  color: #222;
  border-top: 1px solid #222;
  text-align: center;
}

footer p {
  margin: 20px;
  padding: 20px;
}

/*--------------------------------------------------------------
5.0 Animations
--------------------------------------------------------------*/
/* Arrow back-to-top */
#back-to-top {
  position: fixed;
  right: 15px;
  bottom: 15px;
  z-index: 2;
  width: 24px;
  height: 18px;
  text-align: center;
  line-height: 40px;
  color: white;
  cursor: pointer;
  border: 0;
  text-decoration: none;
  -webkit-transition: opacity 0.2s ease-out;
  transition: opacity 0.2s ease-out;
  opacity: 0;
}

#back-to-top:hover {
  font-weight: bold;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  -webkit-transform: translateY(-3px);
  -ms-transform: translateY(-3px);
  transform: translateY(-3px);
}

#back-to-top.show {
  opacity: 0.7;
}

.arrow-up {
  transform: rotate(180deg);
}

.bounce {
  -moz-animation: bounce 2s infinite;
  -webkit-animation: bounce 2s infinite;
  animation: bounce 2s infinite;
}

@-moz-keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    -moz-transform: translateY(0);
    transform: translateY(0);
  }

  40% {
    -moz-transform: translateY(-10px);
    transform: translateY(-10px);
  }

  60% {
    -moz-transform: translateY(-5px);
    transform: translateY(-5px);
  }
}

@-webkit-keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }

  40% {
    -webkit-transform: translateY(-10px);
    transform: translateY(-10px);
  }

  60% {
    -webkit-transform: translateY(-5px);
    transform: translateY(-5px);
  }
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    -moz-transform: translateY(0);
    -ms-transform: translateY(0);
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }

  40% {
    -moz-transform: translateY(-10px);
    -ms-transform: translateY(-10px);
    -webkit-transform: translateY(-10px);
    transform: translateY(-10px);
  }

  60% {
    -moz-transform: translateY(-5px);
    -ms-transform: translateY(-5px);
    -webkit-transform: translateY(-5px);
    transform: translateY(-5px);
  }
}

