/*--------------------------------------------------------------
6.0 Media queries
--------------------------------------------------------------*/
/* tablets and above */
@media only screen and (min-width: 768px) {
  html {
    font-size: 16px;
  }

  .tablet {
    display: block;
  }

  .mobile {
    display: none;
  }

  #header-img {
    margin-top: 16px;
    z-index: 9999;
  }

  #nav-bar {
    display: -webkit-inline-box;
    display: -webkit-inline-flex;
    display: -ms-inline-flexbox;
    display: inline-flex;
    -webkit-box-pack: end;
    -webkit-justify-content: flex-end;
    -ms-flex-pack: end;
    justify-content: flex-end;
  }

  .nav-gradient {
    background-color: #222;
    box-shadow: 8px 10px 20px 0px rgba(46, 61, 73, 0.15);
  }

  #nav-bar a {
    font-size: 18px;
    -webkit-transition: 0.3s;
    transition: 0.3s;
  }

  #nav-bar a:hover {
    color: #fff;
    opacity: 0.6;
  }

  .header-bottom p:first-child {
    font-size: 32px;
  }

  .header-bottom p:nth-child(2) {
    font-size: 24px;
    font-weight: 100;
  }

  .menu-icon-flex img {
    position: fixed;
  }

  .section p {
    font-size: 20px;
  }

  .img-bg-dynamics {
    height: 500px;
    background-image: url(img/BMW-S1000R-front-big.jpg);
    /* background-attachment: fixed; */
  }

  .img-bg-engine {
    height: 920px;
    background-image: url(img/BMW-S1000R-willy-big.jpg);
    background-position: 30%;
  }

  .img-bg-specifications {
    height: 920px;
    background-image: url(img/BMW-S1000R-bg3-big.jpg);
  }

  .form-group input {
    width: 50%;
  }

  #back-to-top {
    right: 32px;
    bottom: 32px;
  }
}

/* only tablets */
@media only screen and (min-width: 767px) and (max-width: 1024px) {
  .title {
    margin-top: 64px;
  }
}

/* only desktop or large tablets */
@media (min-width: 1024px) {
  .title {
    position: absolute;
    top: 200px;
    left: 200px;
  }
}

